import React from 'react';

import PromiseComp from '../../skeleton/containers/PromiseComp';

import { getAuthority } from './authority';

export type IAuthorityType =
  | undefined
  | string
  | string[]
  | Promise<boolean>
  | ((currentAuthority?: string[]) => IAuthorityType);

/**
 * 通用权限检查方法
 * Common check permissions method
 * @param { 权限判定 | Permission judgment } authority
 * @param { 你的权限 | Your permission description } currentAuthority
 * @param { 通过的组件 | Passing components } target
 * @param { 未通过的组件 | no pass components } Exception
 */
export const check = <T, K>(
  authority: IAuthorityType,
  currentAuthority: string[],
  target: T,
  Exception: K,
): T | K | React.ReactNode => {
  // 没有判定权限.默认查看所有
  // Retirement authority, return target;
  if (!authority) {
    return target;
  }

  // 数组处理
  if (Array.isArray(authority)) {
    if (Array.isArray(currentAuthority)) {
      if (currentAuthority.some(item => authority.includes(item))) {
        return target;
      }
    } else if (authority.includes(currentAuthority)) {
      return target;
    }
    return Exception;
  }

  // string 处理
  if (typeof authority === 'string') {
    if (Array.isArray(currentAuthority)) {
      if (currentAuthority.some(item => authority === item)) {
        return target;
      }
    } else if (authority === currentAuthority) {
      return target;
    }
    return Exception;
  }

  // Promise 处理
  if (authority instanceof Promise) {
    return (
      <PromiseComp<T, K> ok={target} error={Exception} promise={authority} />
    );
  }

  // Function 处理
  if (typeof authority === 'function') {
    try {
      // 传入当前的权限判断是否满足要求
      const bool = authority(currentAuthority);

      // 函数执行后返回值是 Promise
      if (bool instanceof Promise) {
        return (
          <PromiseComp<T, K> ok={target} error={Exception} promise={bool} />
        );
      }

      if (bool) {
        return target;
      }

      return Exception;
    } catch (error) {
      throw error;
    }
  }
  throw new Error('unsupported parameters');
};

export function checkPermissions<T, K>(
  authority: IAuthorityType,
  target: T,
  Exception: K,
): T | K | React.ReactNode {
  return check<T, K>(authority, getAuthority(), target, Exception);
}
